import React from 'react'
import classes from './Dialogs.module.css'
import DialogItem from './DialogItem/DialogItem'
import Message from './Message/Message'
import { Field } from 'redux-form'
import { reduxForm } from 'redux-form'
import { Textarea } from '../Common/FormsControls/FormsControls'
import {
  maxValueLength,
  minValueLength,
  required,
} from '../../utils/validate/validate'

let maxLength10 = maxValueLength(10)
let minLength5 = minValueLength(5)

function SendMessageFrom(props) {
  return (
    <form onSubmit={props.handleSubmit}>
      <Field
        component={Textarea}
        name='message'
        validate={[required, maxLength10, minLength5]}
      />
      <button>Add message</button>
    </form>
  )
}

let SendMessageReduxForm = reduxForm({ form: 'sendDialogMessage' })(
  SendMessageFrom
)

function Dialogs(props) {
  let dialogsElements = props.dialogsElements.dialogData.map((name) => (
    <DialogItem key={name.id} name={name.name} id={name.id} />
  ))
  let messagesElements = props.dialogsElements.messagesData.map((message) => (
    <Message
      key={message.id}
      message={message.message}
      avatar={message.avatar}
    />
  ))

  function onSubmit(data) {
    props.addNewMessage(data.message)
  }

  return (
    <div className={classes.body}>
      <div className={classes.dialogs}>
        <ul>{dialogsElements}</ul>
      </div>
      <div className={classes.messages}>
        <ul>{messagesElements}</ul>
        <SendMessageReduxForm onSubmit={onSubmit} />
      </div>
    </div>
  )
}

export default Dialogs
